import Vue from "vue";
import Vuex from "vuex";
import PersistedState from "vuex-persistedstate";
Vue.use(Vuex);
var state = {
  // AUTH_TOKEN: "aksddkdm"
  AUTH_TOKEN: null,
  activeIndex: '/'
};
var mutations = {
  setActiveIndex: function setActiveIndex(state, newValue) {
    state.activeIndex = newValue;
  },
  setToken: function setToken(state, newValue) {
    state.AUTH_TOKEN = newValue;
  },
  removeToken: function removeToken(state) {
    state.AUTH_TOKEN = null;
  }
};
var actions = {};
var store = new Vuex.Store({
  state: state,
  mutations: mutations,
  actions: actions,
  plugins: [PersistedState({
    storage: window.sessionStorage
  })]
});
export default store;